* {
  margin: 0;
  padding: 0;
}

html, body {
  box-sizing: border-box;
  min-height: 100%;
  background: #fffbbe;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100%;
}

header {
  background-color: #a374d5;
  padding: 20px 5px;
  text-align: center;
}

header h3 {
  font-size: 48px;
}

header .desc {
  padding: 24px 8px;
  font-size: 18px;
}

.form {
  background: #be97dc;
  text-align: center;
  padding: 25px 0;
}

.form input {
  width: 320px;
  border: none;
  font-size: 32px;
  outline: none;
  padding: 4px;
}

.result {
  text-align: center;
  height: 100%;
  padding-top: 20px;
}

.result ul li {
  padding: 5px;
    font-size: 24px;
    font-family: cursive;
}